.statistics {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 350px;
  background-color: white;
  margin: 0 auto;
}
.title {
  text-transform: uppercase;
  color: #596066;
  font-size: 30px;
  font-weight: normal;
  margin: 25px 0;
}
.stat-list {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;
  display: flex;
}
.statistics .item {
  width: calc(100% / 5);
  display: flex;
  flex-direction: column;
  color: white;
  & .label {
    font-size: 20px;
    padding-top: 10px;
    text-transform: lowercase;
  }
}

.percentage {
  margin-top: 8px;
  padding-bottom: 10px;
  font-size: 25px;
}
